<template>
  <div class="address">
    <nut-cell  :desc="text" is-link @click="showAddressOther"></nut-cell>
  <nut-address
      v-model:visible="showPopupOther"
      type="exist"
      :exist-address="existAddress"
      :province="province"
      :city="city"
      :country="country"
      :town="town"
      :back-btn-icon="backBtnIcon"
      @close="close"
      @selected="selected"
      custom-and-exist-title="选择其他地址"
      @switch-module="switchModule"
      @close-mask="closeMask"
  ></nut-address>
  </div>
</template>

<script>
  import { ref,reactive,toRefs } from 'vue';
  export default {
    setup() {
        const showPopupOther = ref(false);
        const address = reactive({
          province:[
            { id: 1, name: '北京' },
            { id: 2, name: '广西' },
            { id: 3, name: '江西' },
            { id: 4, name: '四川' }
          ],
          city:[
            { id: 7, name: '朝阳区' },
            { id: 8, name: '崇文区' },
            { id: 9, name: '昌平区' },
            { id: 6, name: '石景山区' }
          ],
          country:[
            { id: 3, name: '八里庄街道' },
            { id: 9, name: '北苑' },
            { id: 4, name: '常营乡' }
          ],
          town:[]
        })
        const existAddress = ref([
          {
            id: 1,
            addressDetail: '',
            cityName: '次渠镇',
            countyName: '通州区',
            provinceName: '北京市',
            selectedAddress: true,
            townName: '',
            name: '探探鱼',
            phone: '182****1718'
          },
          {
            id: 2,
            addressDetail: '',
            cityName: '钓鱼岛全区',
            countyName: '',
            provinceName: '钓鱼岛',
            selectedAddress: false,
            townName: '',
            name: '探探鱼',
            phone: '182****1718'
          },
          {
            id: 3,
            addressDetail: '京东大厦',
            cityName: '大兴区',
            countyName: '科创十一街18号院',
            provinceName: '北京市',
            selectedAddress: false,
            townName: '',
            name: '探探鱼',
            phone: '182****1718'
          }
        ]);
        const backBtnIcon = ref('left')
        const text = ref('请选择地址')

        const showAddressOther = () => {
          showPopupOther.value = true;
        };

        const close = val => {
          if (val.type == 'exist') {
            const {provinceName,cityName,countyName,townName,addressDetail} = val.data
            text.value = provinceName + cityName + countyName + townName + addressDetail;
          } else {
            text.value = val.data.addressStr;
          }
        };

        // eslint-disable-next-line no-unused-vars
        const selected = (prevExistAdd, nowExistAdd, arr) => {
          console.log(prevExistAdd);
          console.log(nowExistAdd);
        };

        const switchModule = cal => {
          if (cal.type == 'custom') {
            console.log('点击了“选择其他地址”按钮');
          } else {
            console.log('点击了自定义地址左上角的返回按钮');
          }
        };

        const closeMask = val => {
          console.log('关闭弹层', val);
        };

        return { showPopupOther, text, existAddress,showAddressOther, switchModule, closeMask, close, selected, backBtnIcon, ...toRefs(address) };
    }
  }
</script>

<style scoped>
.address{
  position: relative;
  flex: 1;
  height: 100%;
  border: 0px red solid;
  text-align: left;
  line-height: 45px;
  margin-left: 30px;
}
.address::after{
  background: #bfbfbf;
  content: "";
  height: 0.6rem;
  margin-top: -0.25rem;
  position: absolute;
  right: 0;
  top: 50%;
  width: 0.8px;
}
.address::before{
  background: url(//jstatic.3.cn/static/icon_location_jd@2x.9ca2376c.png);
  background-size: 12px 12px;
  content: "";
  height: 12px;
  left: -15px;
  margin-top: -7px;
  position: absolute;
  top: 50%;
  width: 12px;
}
.address >>> .nut-cell{
   position: relative;
    display: flex;
    width: 100%;
    line-height: 20px;
    margin: 0px 0;
    box-shadow: 0 0px 0px #edeef1;
    padding: 13px 2px;
    height:100%;
    border: 0px red solid;
    border-radius: 0px;
}
.address >>> .nut-icon-right:before {
    content: "" !important;
}
.address >>> .nut-cell__value{
  flex: 1;
  height: 20px;
  font-size: 13px;
  color: #262626;
  overflow: hidden !important;
  text-overflow:ellipsis !important;
  white-space: nowrap !important;
  -webkit-line-clamp: 1 !important;
  display: -webkit-box !important;
  -webkit-box-orient: vertical !important;
  border: 0px red solid;
  text-align: left !important;
}
</style>