<template>
  <div>
    <SearchBar></SearchBar>
    <nut-category :category="category" @change="change">
      <nut-categorypane :categoryChild="categoryChild" @onChange="onChange">
      </nut-categorypane>
    </nut-category>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted } from 'vue';
import SearchBar from "@/components/SearchBar.vue"
export default {
  setup() {
    const data = reactive({
      categoryInfo: {},
      category: [{}],
      categoryChild: [{}]
    });

    onMounted(() => {
      setTimeout(() => {
        getData();
      }, 500);
    });

    const getData = () => {
      fetch('//storage.360buyimg.com/nutui/3x/categoryData.js')
        .then((response) => response.json())
        .then((res) => {
          const { categoryInfo, categoryChild } = res;
          data.categoryInfo = categoryInfo;
          data.category = categoryInfo.category;
          data.categoryChild = categoryChild;
        })
        .catch((err) => console.log('Oh, error', err)); 
    };

    const change = (index) => {
      data.categoryChild = [].concat(data.categoryInfo.category[index + 1].children);
    };
    const onChange =()=>{
        console.log("当前分类数据");
    }
    return {
      onChange,
      change,
      ...toRefs(data)
    }
  },
  components:{
    // eslint-disable-next-line vue/no-unused-components
    SearchBar
  },
  data(){
    return{
      
    }
  }
};
</script>

<style>
.eing{
  min-width: 320px;
  max-width: 630px;
  width: 100%;
  height: 569px;
  overflow: hidden;
  border: 0px red solid;
}
</style>