// 1.导入 Vue和VueRouter包
import {createRouter,createMemoryHistory}  from "vue-router";



// 3.创建路由的实例对象

// 3.1导入所需的组件
import classify from "@/views/classify/classify.vue"
import home from "@/views/home/home.vue"
import my from "@/views/my/my.vue"
import ShoppingTrolley from "@/views/ShoppingTrolley/ShoppingTrolley.vue"
import find from "@/views/find/find.vue"
import Commoditypage from "@/views/Commoditypage/Commoditypage.vue"
const router=new createRouter(
  {
    history:createMemoryHistory(),
    routes: [
      //默认主页
      {path:'/',redirect:'/home'},//主页
      {path:"/home",component:home},
      {path:"/classify",component:classify},
      {path:"/my",component:my},
      {path:"/ShoppingTrolley",component:ShoppingTrolley},
      {path:"/find",component:find},
      {path:"/Commoditypage",component:Commoditypage}
    ]
  }
);

// 4.向外共享路由的实例对象
export default router
